import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PageComponent } from './page/page.component'; /**deprecated */
import { AvailabilityComponent } from './availability/availability.component';
import { EmployeesComponent } from "./employees/employees.component";
import { ProjectComponent } from './project/project.component';
import { RegistriesComponent } from './registries/registries.component';
import { ReportsComponent } from './reports/reports.component';
import { StatisticsComponent } from './statistics/statistics.component';
import { HomeComponent } from './home/home.component';
import { NavbarComponent } from './navbar/navbar.component';
import { EmployeeListComponent } from "./employees/employee-list/employee-list.component";
import { EmployeeComponent } from "./employees/employee/employee.component";
import { LoginComponent } from './login/login.component';
import { MsalGuard } from "@azure/msal-angular";

/**PageComponent je component koji se pojavljuje na inicijalizaciji page-a a ne Home
Razlog zašto: brži loading jer se ne koristi lazy load metoda.
Kasnije se može putem router-link-a unutar komponente odrediti bolji landing page ali 
sada kako se koristi samo slika, radi demostracije je generaliziran da se duplicira**/

/** 20,05,2020: Ne diraj dole komponente debug sam radio 2 dana i ovo mi palo na pamet */

const routes: Routes = [
  {
    path: "", component: HomeComponent
  },
  { path: "Home", component: HomeComponent },
  { path: "availability", component: AvailabilityComponent },
  { path: "employees", component: EmployeesComponent },
  { path: "employee-list", component: EmployeeListComponent },
  { path: "project", component: ProjectComponent },
  { path: "registries", component: RegistriesComponent },
  { path: "reports", component: ReportsComponent },
  { path: "statistics", component: StatisticsComponent },
  { path: "Navbartest", component: NavbarComponent },
  { path: "employee", component: EmployeeComponent },
  { path: "login", component: LoginComponent, canActivate: [MsalGuard] }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
